.social-list {
  @include reset-list;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  order: 1;
  margin: 0 5px -2px 0;
}

.social-list__item {
  margin-top: 5px;

  &:first-child {
    margin-top: 0;
  }
}

.social-list__link {
  width: 22px;
  height: 12px;

  &--ok {
    width: 20px;
    height: 20px;
  }
}

